.project-card {
    margin: 20px auto;
}

.project-card:hover{
    cursor: pointer;
    box-shadow: 10px 10px 5px gray;
}

.project-card img {
    width: 100%;
    height: 100%;
}

.card-header {
    min-height: 3rem;
    height: auto;
    background-color: rgb(192, 201, 162);
    border: none;
    text-align: center;
    padding: 2px;
}


.card-body {
    height: 250px;
    padding: 0;
}

.card-footer {
    height: 90px;
    background-color: rgb(192, 201, 162);
    border: none;
    font-weight: bolder;
    text-align: center;
    text-overflow: clip;
}